import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Store } from '@ngrx/store';
import { Observable, Subscription } from 'rxjs';
import { CoachDTO } from '@app/modulos/coaching/modelo/coaches.model';
import { tap } from 'rxjs/operators';
import { ActivatedRoute, Router } from '@angular/router';
import { Sesion } from '@app/modulos/coaching/modelo/sesion.model';
import { CargandoService } from '@app/shared/servicios/cargando.service';
import * as momentTimezone from 'moment-timezone';
import { ListaCoachingUsuarioMensualEdicionComponent } from '../../componentes/lista-coaching-usuario-mensual-edicion/lista-coaching-usuario-mensual-edicion.component';
import { MatDialog } from '@angular/material/dialog';
import { environment } from '@env/environment';
import { SesionAgendadas } from '@app/modulos/coaching/modelo/sesionesAgendadas.model';
import { CoachingApiService } from '@coaching/servicios/coaching-api.service';

@Component({
  selector: 'app-lista-coaching-usuario-mensual',
  templateUrl: './lista-coaching-usuario-mensual.component.html',
  styleUrls: ['./lista-coaching-usuario-mensual.component.scss'],
})
export class ListaCoachingUsuarioMensualComponent implements OnInit, OnDestroy {
  idUsuario: string;
  idCoach: string;
  idSesion: string;
  cargando = false;
  columnas: number;

  formulario: FormGroup;
  coaches: CoachDTO[] = [];
  sesiones: Sesion[] = [];
  ahorrador: {
    nombreCompleto: string;
    dineroDisponible: number;
    tieneAhorroDesbloqueado: boolean;
  };
  sesionesVigentes$: Observable<Sesion[]>;
  sesionesAgendadas: SesionAgendadas[];

  coaches$: Observable<CoachDTO[]>;
  existeSesionExpirando: boolean;
  isAdmin: boolean;
  zonaHoraria = environment.zone;
  private suscripciones: Subscription[] = [];
  private idCoachSeleccionado: string;

  constructor(
    private fb: FormBuilder,
    private store: Store<any>,
    private router: Router,
    private route: ActivatedRoute,
    private cargandoService: CargandoService,
    private coachingApiService: CoachingApiService,
    public dialog: MatDialog
  ) {}

  ngOnDestroy() {
    this.suscripciones.forEach((suscripcion) => suscripcion.unsubscribe());
  }

  crearFormulario(fb: FormBuilder): FormGroup {
    return fb.group({
      fecha: fb.control('', [Validators.required]),
      coach: fb.control(''),
    });
  }

  mostrarSesionesAgendada(idCoach: string | number | any) {
    if (idCoach) {
      this.idCoachSeleccionado = idCoach;
      this.cargando = true;
      this.cargandoService.mostrarCargando();
      this.coachingApiService
        .consultarSesionesAgendadasCoach(
          idCoach,
          encodeURIComponent(this.zonaHoraria)
        )
        .subscribe((data: any) => {
          this.sesionesAgendadas = data.sesiones.map((value) => {
            value.nombreAhorrador = value.resumen;
            value.idAhorrador = value.idAhorradores[0];
            value.fechaInicioMoment = momentTimezone(value.fechaInicio).tz(
              this.zonaHoraria
            );
            value.fechaFinMoment = momentTimezone(value.fechaFin).tz(
              this.zonaHoraria
            );
            return value;
          });
          this.cargando = false;
          this.cargandoService.ocultarCargando();
        });
    } else {
      this.idCoachSeleccionado = null;
      this.sesionesAgendadas = new Array<SesionAgendadas>();
    }
  }

  ngOnInit() {
    this.idCoach = this.route.snapshot.paramMap.get('idUsuario');
    this.existeSesionExpirando = false;
    this.columnas = this.getColumnas();
    this.coaches$ = this.store
      .select('coaching', 'coaches')
      .pipe(tap((coaches) => (this.coaches = coaches)));
    this.sesionesVigentes$ = this.store.select('coaching', 'sesionesVigentes');
    this.formulario = this.crearFormulario(this.fb);
    this.store.select('coaching', 'rol').subscribe((value) => {
      this.isAdmin = value === 'ADMIN';
    });
    this.store.select('coaching', 'zonaHoraria').subscribe((value) => {
      this.zonaHoraria = value;
      this.mostrarSesionesAgendada(this.idCoachSeleccionado);
    });
  }

  getColumnas() {
    if (this.getInnerWidth() <= 400) {
      return 1;
    } else {
      return this.getInnerWidth() <= 600 ? 2 : 3;
    }
  }

  getInnerWidth() {
    return window.innerWidth;
  }

  onRowClick(sesion: SesionAgendadas) {
    const dialogRef = this.dialog.open(
      ListaCoachingUsuarioMensualEdicionComponent,
      {
        panelClass: 'modal-editar-sesiones',
        data: sesion,
      }
    );

    dialogRef.afterClosed().subscribe((result) => {
      this.mostrarSesionesAgendada(this.formulario.get('coach').value);
    });
  }

  get fecha() {
    return this.formulario.get('fecha');
  }

  get coach() {
    return this.formulario.get('coach');
  }

  get hora() {
    return this.formulario.get('hora');
  }

  public recargarConsulta() {
    if (this.idCoachSeleccionado) {
      this.mostrarSesionesAgendada(this.idCoachSeleccionado);
    }
  }
}
