import { NgModule } from '@angular/core';
import { CommonModule, DatePipe } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { SharedModule } from '@app/shared/shared.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CoachingComponent } from '@app/modulos/coaching/coaching.component';
import { DetalleCoachingUsuarioComponent } from './vista/detalle-coaching-usuario/detalle-coaching-usuario.component';
import { MaterialModule } from '@app/modulos/material/material.module';
import { DetalleSesionCoachGuard } from '@app/modulos/coaching/guards/detalle-sesion-coach.guard';
import { SesionesCoachGuard } from '@app/modulos/coaching/guards/sesiones-coach.guard';
import { IdCoachInvalidoComponent } from '@app/modulos/coaching/vista/id-coach-invalido/id-coach-invalido.component';
import { CoachInvalidoGuard } from '@app/modulos/coaching/guards/coach-invalido.guard';
import { ListaSesionesComponent } from './componentes/lista-sesiones/lista-sesiones.component';
import { ListaSesionesCardsComponent } from './componentes/lista-sesiones-cards/lista-sesiones-cards.component';
import { ListaCoachingUsuarioMensualComponent } from './vista/lista-coaching-usuario-mensual/lista-coaching-usuario-mensual.component';
import { ListaCoachingUsuarioMensualEdicionComponent } from './componentes/lista-coaching-usuario-mensual-edicion/lista-coaching-usuario-mensual-edicion.component';
import { MenuIzquierdoComponent } from '@app/modulos/coaching/componentes/menu-izquierdo/menu-izquierdo.component';
import { InicioComponent } from './vista/inicio/inicio.component';
import { ProgramarSesionesComponent } from './vista/programar-sesiones/programar-sesiones.component';
import { ListaSesionesCoachGuard } from '@app/modulos/coaching/guards/lista-sesiones-coach.guard';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { ModalOtpSesionComponent } from './componentes/modal-otp-sesion/modal-otp-sesion.component';
import { DetalleBienestarFinancieroComponent } from './componentes/detalle-bienestar-financiero/detalle-bienestar-financiero.component';
import { LeadGenericoComponent } from '@app/modulos/coaching/vista/lead-generico/lead-generico.component';
import { HumanizePipe } from '@app/shared/pipes/HumanizePipe.pipe';
import { LeadsComponent } from '@app/modulos/coaching/vista/tareas/leads.component';
import { ModalComentarioTareaGenericaComponent } from '@app/modulos/coaching/componentes/modal-comentario-tarea-generica/modal-comentario-tarea-generica.component';
import { FormularioAgendarSesionComponent } from './componentes/formulario-agendar-sesion/formulario-agendar-sesion.component';
import { ListaSesionesVencidasComponent } from './componentes/lista-sesiones-vencidas/lista-sesiones-vencidas.component';

const routes: Routes = [
  {
    path: 'id-coach-invalido',
    component: CoachingComponent,
    canActivate: [CoachInvalidoGuard],
    children: [
      { path: '', component: IdCoachInvalidoComponent, pathMatch: 'full' },
    ],
  },
  {
    path: '',
    component: CoachingComponent,
    canActivate: [SesionesCoachGuard],
    children: [
      {
        path: '',
        component: InicioComponent,
        canActivate: [SesionesCoachGuard, ListaSesionesCoachGuard],
      },
      {
        path: 'sesion/:idSesion',
        component: DetalleCoachingUsuarioComponent,
        canActivate: [DetalleSesionCoachGuard],
      },
      { path: 'mensual', component: ListaCoachingUsuarioMensualComponent },
      { path: 'programar-sesiones', component: ProgramarSesionesComponent },
      { path: 'leads', component: LeadsComponent },
      { path: 'detalle-generico/:id_lead', component: LeadGenericoComponent },
    ],
  },
];

@NgModule({
  declarations: [
    CoachingComponent,
    DetalleCoachingUsuarioComponent,
    IdCoachInvalidoComponent,
    ListaSesionesComponent,
    ListaSesionesCardsComponent,
    ListaCoachingUsuarioMensualComponent,
    ListaCoachingUsuarioMensualEdicionComponent,
    MenuIzquierdoComponent,
    InicioComponent,
    ProgramarSesionesComponent,
    ModalOtpSesionComponent,
    DetalleBienestarFinancieroComponent,
    LeadsComponent,
    LeadGenericoComponent,
    ModalComentarioTareaGenericaComponent,
    FormularioAgendarSesionComponent,
    ListaSesionesVencidasComponent,
  ],
  imports: [
    CommonModule,
    MaterialModule,
    FormsModule,
    ReactiveFormsModule,
    SharedModule,
    ScrollingModule,
    RouterModule.forChild(routes),
  ],
  providers: [
    ListaSesionesCoachGuard,
    DetalleSesionCoachGuard,
    SesionesCoachGuard,
    CoachInvalidoGuard,
    DatePipe,
    HumanizePipe,
  ],
  entryComponents: [
    ListaCoachingUsuarioMensualEdicionComponent,
    ModalOtpSesionComponent,
    ModalComentarioTareaGenericaComponent,
  ],
})
export class CoachingModule {}
