import { Component, Input, OnInit } from '@angular/core';
import { Sesion } from '@app/modulos/coaching/modelo/sesion.model';
import { CoachDTO } from '@app/modulos/coaching/modelo/coaches.model';

@Component({
  selector: 'app-lista-sesiones-cards',
  templateUrl: './lista-sesiones-cards.component.html',
  styleUrls: ['./lista-sesiones-cards.component.scss'],
})
export class ListaSesionesCardsComponent implements OnInit {
  @Input() sesiones: Sesion[] = [];
  @Input() coaches: CoachDTO[] = [];

  constructor() {}

  ngOnInit() {}

  obtenerNombreCoach(idCoach: string): string {
    const datosCoach = this.coaches.find((coach) => coach.id === idCoach);
    return datosCoach
      ? datosCoach.nombreCompleto
      : idCoach
      ? 'Desconocido'
      : '';
  }
}
