import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Sesion } from '@app/modulos/coaching/modelo/sesion.model';
import { Tema } from '@app/modulos/coaching/modelo/temas.model';
import { CoachDTO } from '@app/modulos/coaching/modelo/coaches.model';

export interface TablaModel {
  fecha: Date;
  coach: string;
  tema: string;
  estado: string;
}

@Component({
  selector: 'app-lista-sesiones',
  templateUrl: './lista-sesiones.component.html',
  styleUrls: ['./lista-sesiones.component.scss'],
})
export class ListaSesionesComponent implements OnInit {
  _sesiones: Sesion[] = [];
  @Input() coaches: CoachDTO[] = [];
  @Input() temas: Tema[] = [];

  displayedColumns: string[] = ['fecha', 'hora', 'coach', 'tema', 'estado'];
  dataSource: MatTableDataSource<TablaModel>;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor() {}

  get sesiones(): Sesion[] {
    return this._sesiones;
  }

  @Input()
  set sesiones(sesiones: Sesion[]) {
    this._sesiones = this.ordenar(sesiones);
    const datosTabla = this._sesiones.map((sesion) => {
      return {
        fecha: sesion.fechaInicioMoment,
        coach: sesion.coach.idCoach,
        tema: sesion.tema,
        estado: sesion.estadoSesion.toString(),
      };
    });
    this.refrescarDatosTabla(datosTabla);
  }

  private ordenar(sesiones: Sesion[]) {
    return sesiones.sort((a, b) =>
      new Date(b.fechaInicio).getTime() - new Date(a.fechaInicio).getTime()
    );
  }

  ngOnInit() {}

  refrescarDatosTabla(datosTabla) {
    this.dataSource = new MatTableDataSource(datosTabla);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  obtenerCoach(idCoach: string) {
    const coachSesion = this.coaches.find((coach) => coach.id === idCoach);
    return coachSesion
      ? coachSesion.nombreCompleto
      : idCoach
      ? 'Desconocido'
      : '';
  }
}
