import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { FormControl, Validators } from '@angular/forms';
import { CustomModalService } from '@app/shared/servicios/custom-modal.service';
import { CustomModalType } from '@app/modulos/dashboard/modelo/enums/CustomModalTypes.enum';
import { CoachingApiService } from '@coaching/servicios/coaching-api.service';

@Component({
  selector: 'app-modal-otp-sesion',
  templateUrl: './modal-otp-sesion.component.html',
  styleUrls: ['./modal-otp-sesion.component.scss'],
})
export class ModalOtpSesionComponent implements OnInit {
  otpForm: FormControl;
  cargando: boolean;

  constructor(
    public dialogRef: MatDialogRef<ModalOtpSesionComponent>,
    private coachingApiService: CoachingApiService,
    private customModal: CustomModalService,
    @Inject(MAT_DIALOG_DATA)
    public data: {
      coach: { nombreCompleto; id };
      fecha;
      hora: { inicio; fin };
      ahorrador: { nombre; id };
      tema;
    }
  ) {}

  ngOnInit() {
    this.cargando = false;
    this.otpForm = new FormControl('', [
      Validators.required,
      Validators.minLength(6),
    ]);
  }

  cerrar(): void {
    this.dialogRef.close();
  }

  validar() {
    const bodyOtp = {
      idAhorrador: this.data.ahorrador.id,
      otp: this.otpForm.value,
      flujoOtp: 'CONFIRMAR_SESION',
    };
    this.coachingApiService.confirmarOTP(bodyOtp).then(
      (value) => {
        this.dialogRef.close({ registarSesion: true });
      },
      (error) => {
        console.error('Error confirmando otp: ', error);
        this.customModal.abrirModal(CustomModalType.ERROR, {
          mensaje: 'No se pudo validar el código OTP',
        });
      }
    );
  }
}
