import { Injectable } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivate,
  Router,
  RouterStateSnapshot,
} from '@angular/router';
import { Store } from '@ngrx/store';
import { Observable, of } from 'rxjs';
import { flatMap, map, take } from 'rxjs/operators';
import { CONSULTA_SESIONES_COACH_EXPIRED, CONSULTA_SESIONES_COACH_SUCCESS } from '../state-management/actions/coaching.action';
import { SesionesCoach } from '../modelo/sesionesCoach.model';
import { CoachingApiService } from '@coaching/servicios/coaching-api.service';
import { EstadoSesion } from '../modelo/estado-sesion.enum';
import { Sesion } from '../modelo/sesion.model';
@Injectable()
export class SesionesCoachGuard implements CanActivate {
  constructor(
    private store: Store<any>,
    private router: Router,
    private coachingApiService: CoachingApiService
  ) { }

  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean> | boolean {
    return this.store.select('usuario', 'uuid').pipe(
      take(1),
      flatMap((uiid) => {
        if (uiid) {
          return this.coachingApiService.consultarSesiones(uiid).pipe(
            map((sesiones: SesionesCoach) => {
              this.ordenarSesionesVigentes(sesiones);
              this.ordenarSesionesVencidas(sesiones);
              return true;
            })
          );
        } else {
          this.router.navigate(['coaching/id-coach-invalido']);
          return of(false);
        }
      })
    );
  }

  ordenarSesionesVigentes(sesionesCoach: SesionesCoach) {
    const sesionesVigentes: SesionesCoach = { ...sesionesCoach };
    sesionesVigentes.sesionesVigentes = this.ordenarSesion(sesionesVigentes.sesionesVigentes.
      filter(sesion => sesion.estadoSesion !== EstadoSesion.VENCIDA));
    this.store.dispatch({
      type: CONSULTA_SESIONES_COACH_SUCCESS,
      payload: sesionesVigentes,
    });
  }

  ordenarSesionesVencidas(sesionesCoach: SesionesCoach) {
    const sesionesVencidas: SesionesCoach = { ...sesionesCoach };
    sesionesVencidas.sesionesVigentes = this.ordenarSesion(sesionesVencidas.sesionesVigentes.
      filter(sesion => sesion.estadoSesion === EstadoSesion.VENCIDA));

    this.store.dispatch({
      type: CONSULTA_SESIONES_COACH_EXPIRED,
      payload: sesionesVencidas,
    });
  }

  private ordenarSesion(sesiones: Sesion[]) {
    return sesiones.sort((a, b) => new Date(b.fechaInicio).getTime() - new Date(a.fechaInicio).getTime());
  }

}
