import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { DashboardService } from '@dashboard/servicios/dashboard.service';
import { ModalComentarioTareaGenericaComponent } from '../../componentes/modal-comentario-tarea-generica/modal-comentario-tarea-generica.component';
import { CustomModalType } from '@dashboard/modelo/enums/CustomModalTypes.enum';
import { CustomModalService } from '@app/shared/servicios/custom-modal.service';
import { Location } from '@angular/common';
import { ILeadGenerico } from '../../modelo/ILeadGenerico.type';

@Component({
  selector: 'app-tarea-generica',
  templateUrl: './lead-generico.component.html',
  styleUrls: ['./lead-generico.component.scss'],
})
export class LeadGenericoComponent implements OnInit {
  detalleLead: ILeadGenerico;
  bloquearBoton = false;

  constructor(
    private location: Location,
    private router: Router,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    private dashboardService: DashboardService,
    private customModalService: CustomModalService
  ) {}

  ngOnInit() {
    this.route.params.subscribe((params) => {
      const idLead = +params.id_lead;
      this.dashboardService.consultarTareaPorId(idLead).subscribe(
        (tarea) => (this.detalleLead = tarea),
        (error) => {
          this.router
            .navigate(['/dashboard'])
            .then(() => this.obtenerErrorConsulta(error.data.mensaje))
            .catch((reason) => console.error(reason));
          console.error('ERROR consulta tarea generica', error);
        }
      );
    });
  }

  abrirModalComentario() {
    const modal = this.dialog.open(ModalComentarioTareaGenericaComponent, {
      data: {},
      panelClass: 'medium-screen-dialog',
    });
    modal.afterClosed().subscribe((comentario) => {
      if (comentario) {
        this.detalleLead = Object.assign({}, this.detalleLead, {
          comentario,
        });
        this.finalizarTareaGenerica(this.detalleLead);
      }
    });
    return modal;
  }

  finalizarTareaGenerica(detalle: ILeadGenerico) {
    this.bloquearBoton = true;
    this.dashboardService
      .finalizarTareaGenerica({
        idTarea: detalle.id,
        comentario: detalle.comentario,
      })
      .then(() => {
        this.bloquearBoton = false;
        this.mostarModalSuccess();
        this.location.back();
      })
      .catch((err) => {
        this.bloquearBoton = false;
        this.mostarModalError();
      });
  }

  mostarModalSuccess() {
    this.customModalService.abrirModal(CustomModalType.SUCCESS);
  }

  mostarModalError() {
    this.customModalService.abrirModal(CustomModalType.ERROR);
  }

  private obtenerErrorConsulta(error: string) {
    switch (error) {
      case 'tarea.detalle.pais':
        return this.customModalService.abrirModal(CustomModalType.WARNING, {
          mensaje:
            'La tarea que intenta consultar no corresponde al país en sesión.',
        });
      case 'tarea.detalle.noexiste':
        return this.customModalService.abrirModal(CustomModalType.ERROR);
      default:
        return this.customModalService.abrirModal(CustomModalType.ERROR);
    }
  }
}
