import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl } from '@angular/forms';
import { combineLatest, Observable, of } from 'rxjs';
import {
  catchError,
  debounceTime,
  mergeMap,
  map,
  startWith,
} from 'rxjs/operators';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Tema } from '@app/modulos/coaching/modelo/temas.model';
import { CoachDTO } from '@app/modulos/coaching/modelo/coaches.model';
import { environment } from '@env/environment';
import { CargandoService } from '@app/shared/servicios/cargando.service';
import { CustomModalType } from '@app/modulos/dashboard/modelo/enums/CustomModalTypes.enum';
import { CustomModalService } from '@app/shared/servicios/custom-modal.service';
import { Sesion } from '@app/modulos/coaching/modelo/sesion.model';
import { EstadoSesion } from '@app/modulos/coaching/modelo/estado-sesion.enum';

import * as momentTimezone from 'moment-timezone';
import { ConsultadoNombreAhorradorDTO } from '@app/modulos/coaching/modelo/ahorrador.model';
import { MatDialog } from '@angular/material/dialog';
import { CoachingApiService } from '@coaching/servicios/coaching-api.service';
import { FormularioAgendarSesionComponent } from '@coaching/componentes/formulario-agendar-sesion/formulario-agendar-sesion.component';

const TAMANO_CONSULTA = 10;
const ALTURA_ITEM = 48;
const MAX_ITEMS = 5;

@Component({
  selector: 'app-programar-sesiones',
  templateUrl: './programar-sesiones.component.html',
  styleUrls: ['./programar-sesiones.component.scss'],
})
export class ProgramarSesionesComponent implements OnInit {
  @ViewChild(FormularioAgendarSesionComponent)
  formulario: FormularioAgendarSesionComponent;
  usuario: { id: string; nombre: string; pais: string };
  sesionesTabla: Sesion[];

  // Variables buscador
  inputNombreUsuario = new FormControl();
  idUsuario: string;
  tamanoLista: number;
  paginaConsultar: number;
  listaUsuariosCache: ConsultadoNombreAhorradorDTO[] = [];
  opcionSeleccionada: boolean;
  cargandoBuscador: boolean;
  mostrarSinResultados = false;
  coaches: CoachDTO[] = [];
  temas: Tema[] = [];
  private bloquearBusquedaScroll = false;
  private zonaHoraria = environment.zone;
  private sesiones: Sesion[];

  constructor(
    private fb: FormBuilder,
    private store: Store<any>,
    private router: Router,
    private cargandoService: CargandoService,
    private customModalService: CustomModalService,
    public dialog: MatDialog,
    private coachingApiService: CoachingApiService
  ) {}

  ngOnInit() {
    this.paginaConsultar = 0;
    this.suscribirCambiosInput();
    this.initPagina();
  }

  suscribirCambiosInput() {
    this.inputNombreUsuario.valueChanges
      .pipe(
        startWith(''),
        debounceTime(1000),
        mergeMap((value) => {
          this.paginaConsultar = 0;
          if (this.opcionSeleccionada) {
            this.opcionSeleccionada = false;
            return [];
          }
          this.listaUsuariosCache = [];
          this.bloquearBusquedaScroll = false;
          return this.buscarUsuarios(
            value,
            TAMANO_CONSULTA,
            this.paginaConsultar
          );
        })
      )
      .subscribe(() => {
        this.tamanoLista = this.listaUsuariosCache.length;
      });
  }

  buscarUsuarios(
    value: string,
    cantidad: number,
    pagina: number
  ): Observable<ConsultadoNombreAhorradorDTO[]> {
    if (
      value === null ||
      value === undefined ||
      value.trim() === '' ||
      value.length < 3
    ) {
      this.usuario = null;
      this.reiniciarFormulario(this.formulario, false);
      return of([]);
    } else {
      this.mostrarSinResultados = false;
      const parametros = {
        limite: cantidad,
        campo: value.trim(),
        intervalo: pagina,
      };
      this.cargandoBuscador = true;
      return this.coachingApiService.consultarNombresUsuario(parametros).pipe(
        map((x) => x.usuarios),
        map((listaAhorradores: ConsultadoNombreAhorradorDTO[]) => {
          this.cargandoBuscador = false;
          this.listaUsuariosCache = this.listaUsuariosCache.concat(
            listaAhorradores
          );
          if (this.listaUsuariosCache.length === 0) {
            this.mostrarSinResultados = true;
          }
          if (listaAhorradores.length < cantidad) {
            this.bloquearBusquedaScroll = true;
          }
          return this.listaUsuariosCache;
        }),
        catchError((error) => {
          console.log('Error consultando usuario:', error);
          return [];
        })
      );
    }
  }

  logScroll(event: any) {
    if (!this.cargandoBuscador && !this.bloquearBusquedaScroll) {
      const breakpoint = this.tamanoLista - event - this.tamanoLista * 0.1;
      if (breakpoint <= MAX_ITEMS) {
        this.paginaConsultar++;
        this.buscarUsuarios(
          this.inputNombreUsuario.value,
          TAMANO_CONSULTA,
          this.paginaConsultar
        ).subscribe((value) => {
          this.tamanoLista = value.length;
        });
      }
    }
  }

  obtenerAltura() {
    let altura;
    if (!this.tamanoLista || this.tamanoLista === 0) {
      altura = ALTURA_ITEM;
    } else if (this.tamanoLista < MAX_ITEMS) {
      altura = this.tamanoLista * ALTURA_ITEM;
    } else {
      altura = MAX_ITEMS * ALTURA_ITEM;
    }
    return altura + 'px';
  }

  initPagina() {
    const coaches$ = this.store.select('coaching', 'coaches');
    const temas$ = this.store.select('coaching', 'temas');
    const zonaHoraria$ = this.store.select('coaching', 'zonaHoraria');

    combineLatest([coaches$, temas$, zonaHoraria$]).subscribe((value) => {
      this.coaches = value[0];
      this.temas = value[1];
      this.zonaHoraria = value[2];
    });
  }

  seleccionarOpcion(option: ConsultadoNombreAhorradorDTO, $event) {
    if (option && $event.isUserInput) {
      this.opcionSeleccionada = true;
      this.idUsuario = option.idUsuario;
      this.usuario = {
        id: option.idUsuario,
        nombre: option.nombreCompleto || option.correo,
        pais: option.paisOperacion,
      };
      this.inputNombreUsuario.setValue(this.usuario.nombre);
      this.inputNombreUsuario.updateValueAndValidity();
      this.cargarDatosSesiones();
      this.reiniciarFormulario(this.formulario, true);
      this.sesionesTabla = [];
    }
  }

  private cargarDatosSesiones() {
    this.cargandoService.mostrarCargando();
    this.coachingApiService
      .consultarSesionesAhorrador(this.usuario.id)
      .subscribe((sesionesAhorradorDTO: { sesiones: Sesion[] }) => {
        this.sesiones = this.actualizarFechasSegunZona(
          sesionesAhorradorDTO.sesiones,
          this.zonaHoraria
        );
        this.cargarDataSource();
        this.cargandoService.ocultarCargando();
      });
  }

  private actualizarFechasSegunZona(
    sesionesVigentes: Sesion[],
    zonaHoraria: string
  ) {
    return sesionesVigentes.map((value) => {
      value.fechaInicioMoment = momentTimezone(value.fechaInicio).tz(
        zonaHoraria
      );
      value.fechaFinMoment = momentTimezone(value.fechaFin).tz(zonaHoraria);
      return value;
    });
  }

  private cargarDataSource() {
    this.sesionesTabla = this.sesiones.filter(
      (sesion) =>
        sesion.estadoSesion !== EstadoSesion.NUEVA &&
        sesion.estadoSesion !== EstadoSesion.AGENDADA &&
        sesion.estadoSesion !== EstadoSesion.PREAGENDADA &&
        sesion.estadoSesion !== EstadoSesion.AGENDAMIENTO_PROGRAMADO &&
        sesion.estadoSesion !== EstadoSesion.INICIADA
    );
  }

  mostarModalSuccess(mensaje) {
    this.customModalService.abrirModal(CustomModalType.SUCCESS, { mensaje });
  }

  mostarModalError(mensaje) {
    this.customModalService.abrirModal(CustomModalType.ERROR, { mensaje });
  }

  reiniciarFormulario(
    formulario: FormularioAgendarSesionComponent,
    habilitado: boolean
  ) {
    if (formulario) {
      formulario.reiniciarFormulario();
      if (!habilitado) {
        formulario.disableFormulario();
      } else {
        formulario.enableFormulario();
      }
    }
  }
}
