import { Injectable } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivate,
  Router,
  RouterStateSnapshot,
} from '@angular/router';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';
import { map, take } from 'rxjs/operators';
import { Sesion } from '../modelo/sesion.model';

@Injectable()
export class DetalleSesionCoachGuard implements CanActivate {
  constructor(private store: Store<any>, private router: Router) {}

  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean> | boolean {
    const idSesion = next.params['idSesion'];
    return this.store.select('coaching', 'sesionesVigentes').pipe(
      take(1),
      map((sesions: Sesion[]) => {
        const sesionesUsuario = !sesions
          ? []
          : sesions.filter((sesion) => sesion.idSesion === idSesion);
        if (sesionesUsuario.length === 0) {
          this.router.navigate([`coaching`]);
          return false;
        } else {
          return true;
        }
      })
    );
  }
}
