import { EstadoSesion } from './modelo/estado-sesion.enum';
import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { Sesion } from '@coaching/modelo/sesion.model';
import { CookiesUtilsService } from '@app/shared/servicios/cookies-utils.service';
import {
  ACTUALIZAR_ZONA_HORARIA,
  CONSULTA_COACHES_SUCCESS,
  CONSULTA_SESIONES_COACH_EXPIRED,
  CONSULTA_SESIONES_COACH_SUCCESS,
  CONSULTA_TEMAS_SUCCESS,
} from '@coaching/state-management/actions/coaching.action';
import { Store } from '@ngrx/store';
import { combineLatest, Observable, Subscription, timer } from 'rxjs';
import { CargandoService } from '@app/shared/servicios/cargando.service';
import { SesionesCoach } from '@coaching/modelo/sesionesCoach.model';
import { TemasSesiones } from '@coaching/modelo/temas.model';
import { Coaches } from '@coaching/modelo/coaches.model';
import { MatSidenav } from '@angular/material/sidenav';
import { delay, first, mergeMap } from 'rxjs/operators';
import { environment } from '@env/environment';
import * as momentTimezone from 'moment-timezone';
import { CoachingApiService } from '@coaching/servicios/coaching-api.service';
@Component({
  selector: 'app-coaching',
  templateUrl: './coaching.component.html',
  styleUrls: ['./coaching.component.scss'],
})
export class CoachingComponent implements OnInit, OnDestroy {
  @ViewChild(MatSidenav, { static: true }) sideNav: MatSidenav;
  nombreCoach$: Observable<string>;
  rolUser$: Observable<string>;
  cargando$: Observable<boolean | null>;
  TIEMPO_RECARGA = 1800000;
  zonasHorarias: string[] = [];
  zonaHorariaSeleccionada = environment.zone;
  private suscripciones: Subscription[] = [];

  isAdmin: boolean;

  constructor(
    private store: Store<any>,
    private cargandoService: CargandoService,
    private coachingApiService: CoachingApiService,
    private cookiesService: CookiesUtilsService
  ) {}

  ngOnInit() {
    this.zonasHorarias = momentTimezone.tz.names();
    this.nombreCoach$ = this.store.select('coaching', 'nombreCompleto');
    this.rolUser$ = this.store.select('coaching', 'rol');
    this.cargando$ = this.cargandoService.cargar$.asObservable();
    this.cargarTemas();
    this.cargarCoaches();
    this.cargarSesionesVigentes();
    this.store.select('coaching', 'rol').subscribe((value) => {
      this.isAdmin = value === 'ADMIN';
    });
    this.subscribeSideNavChange();
  }

  ngOnDestroy() {
    this.suscripciones.forEach((value1) => value1.unsubscribe());
    this.suscripciones = [];
  }

  private cargarTemas() {
    this.coachingApiService
      .consultarTemas({
        tipoUsuario: 'COACH',
        pais: environment.envCountry,
      })
      .then((temasSesiones: TemasSesiones) =>
        this.store.dispatch({
          type: CONSULTA_TEMAS_SUCCESS,
          payload: temasSesiones.temas,
        })
      );
  }

  private cargarCoaches() {
    this.coachingApiService
      .consultarCoachesPorPais(environment.envCountry)
      .subscribe((coaches: Coaches) =>
        this.store.dispatch({
          type: CONSULTA_COACHES_SUCCESS,
          payload: coaches.coaches,
        })
      );
  }

  private cargarSesionesVigentes() {
    const zonaHoraria$ = this.store.select('coaching', 'zonaHoraria');
    const sesiones$ = this.store.select('coaching', 'sesionesVigentes');

    this.suscripciones.push(
      combineLatest([zonaHoraria$, sesiones$]).subscribe((value) =>{
        this.programarReintentoConsultaSesiones(value[1], value[0]);
      })
    );
  }

  private programarReintentoConsultaSesiones(
    sesiones: Sesion[],
    zonaHoraria: string
  ) {
    if (sesiones && sesiones.length > 0) {
      const sesion = this.obtenerPrimeraSesionEnFinalizar(sesiones);
      const tiempo =
        this.calcularTiempoParaFinalizarSesion(sesion, zonaHoraria) + 1000;
      this.programarConsultaSesion(tiempo);
    } else {
      this.programarConsultaSesion(this.TIEMPO_RECARGA);
    }
  }

  public programarConsultaSesion(tiempo) {
    this.suscripciones.forEach((suscripciones) => suscripciones.unsubscribe());
    this.suscripciones = [];
    this.suscripciones.push(
      timer().pipe(delay(tiempo))
        .pipe(first())
        .pipe(mergeMap(() => this.store.select('usuario', 'uuid').pipe(first())))
        .pipe(
          mergeMap((idCoach) => {
            return this.coachingApiService.consultarSesiones(idCoach);
          })
        )
        .subscribe((sesionesCoach: SesionesCoach) => {
          this.ordenarSesionesVigentes(sesionesCoach);
          this.ordenarSesionesVencidas(sesionesCoach);
        })
    );

  }

  ordenarSesionesVigentes(sesionesCoach: SesionesCoach) {
    const sesionesVigentes: SesionesCoach = { ...sesionesCoach };
    sesionesVigentes.sesionesVigentes = this.ordenarSesion(sesionesVigentes.sesionesVigentes.filter(
      sesion => sesion.estadoSesion !== EstadoSesion.VENCIDA));
    this.store.dispatch({
      type: CONSULTA_SESIONES_COACH_SUCCESS,
      payload: sesionesVigentes,
    });
  }

  ordenarSesionesVencidas(sesionesCoach: SesionesCoach) {
    const sesionesVencidas: SesionesCoach = { ...sesionesCoach };
    sesionesVencidas.sesionesVigentes = this.ordenarSesion(sesionesVencidas.sesionesVigentes.filter(
      sesion => sesion.estadoSesion === EstadoSesion.VENCIDA));
    this.store.dispatch({
      type: CONSULTA_SESIONES_COACH_EXPIRED,
      payload: sesionesVencidas,
    });
  }

  private ordenarSesion(sesiones: Sesion[]) {
    return sesiones.sort((a, b) => new Date(b.fechaInicio).getTime() - new Date(a.fechaInicio).getTime());
  }

  calcularTiempoParaFinalizarSesion(sesion: Sesion, zonaHoraria: string) {
    const fechaFinSesion = sesion.fechaFinMoment.toDate();
    fechaFinSesion.setMinutes(fechaFinSesion.getMinutes() + 10);
    const fechaFin = this.obtenerFecha(fechaFinSesion, zonaHoraria);
    const fechaActual = this.obtenerFecha(new Date(), zonaHoraria);
    return fechaFin.getTime() - fechaActual.getTime();
  }

  obtenerPrimeraSesionEnFinalizar(sesiones: Sesion[]) {
    const sesionesOrdenadas = sesiones.slice().sort(this.orfenarPorFecha);
    return sesiones ? sesionesOrdenadas[0] : null;
  }

  private orfenarPorFecha(a, b) {
    return new Date(a.fechaFin).getTime() - new Date(b.fechaFin).getTime();
  }

  obtenerFecha(fecha: Date, zonaHoraria: string) {
    const hoy = momentTimezone(fecha);
    return hoy.tz(zonaHoraria).toDate();
  }

  zonaCambiada() {
    this.store.dispatch({
      type: ACTUALIZAR_ZONA_HORARIA,
      payload: this.zonaHorariaSeleccionada,
    });
  }

  subscribeSideNavChange() {
    this.sideNav.openedChange.asObservable().subscribe((opened) => {
      if (!this.obtenerEstadoMenu() && this.sideNav.opened) {
        this.sideNav.close();
      }
    });
  }

  obtenerEstadoMenu() {
    return this.cookiesService.obtenerInformacionCookie('mostrarMenuIzquierdo');
  }

  actualizarEstadoMenu(mostarMenu) {
    this.cookiesService.guardarInformacionCookie(
      'mostrarMenuIzquierdo',
      mostarMenu
    );
  }
}
