import { Injectable } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivate,
  Router,
  RouterStateSnapshot,
} from '@angular/router';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';
import { map, take } from 'rxjs/operators';
import { SesionesCoach } from '../modelo/sesionesCoach.model';

@Injectable()
export class ListaSesionesCoachGuard implements CanActivate {
  constructor(private store: Store<any>, private router: Router) {}

  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean> | boolean {
    return this.store.select('coaching').pipe(
      take(1),
      map((sesionesCoach: SesionesCoach) => {
        const forzarInicio = next.params.mostrarInicio;
        if (
          !sesionesCoach ||
          sesionesCoach.sesionesVigentes.length === 0 ||
          sesionesCoach.sesionesVigentes.length > 1 ||
          forzarInicio
        ) {
          return true;
        } else {
          const idSesion = sesionesCoach.sesionesVigentes[0].idSesion;
          this.router.navigate([
            `coaching/sesion/${idSesion}`,
            {
              idUsuario:
                sesionesCoach.sesionesVigentes[0].ahorrador.idAhorrador,
            },
          ]);
          return false;
        }
      })
    );
  }
}
