import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { ITareasPaginadas } from '@dashboard/modelo/interfaces/ITareasPaginadas.type';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { ITarea } from '@dashboard/modelo/interfaces/ITarea.type';
import { EstadoTarea } from '@dashboard/modelo/enums/EstadosTarea.enum';
import { DashboardService } from '@dashboard/servicios/dashboard.service';
import { Router } from '@angular/router';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { TipoTarea } from '@dashboard/modelo/enums/TipoTarea.enum';
import { CustomModalType } from '@dashboard/modelo/enums/CustomModalTypes.enum';
import { DatePipe } from '@angular/common';
import { CustomModalService } from '@app/shared/servicios/custom-modal.service';
import { MatSort } from '@angular/material/sort';
import { fromEvent } from 'rxjs';
import { HumanizePipe } from '@app/shared/pipes/HumanizePipe.pipe';

@Component({
  selector: 'app-tareas',
  templateUrl: './leads.component.html',
  styleUrls: ['./leads.component.scss'],
})
export class LeadsComponent implements OnInit {
  @ViewChild('filter', { static: true }) filter: ElementRef;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  leads: ITarea[];
  leadsFiltrados: ITarea[];
  length = 100;
  pageSize = 10;
  pageSizeOptions = [10, 25, 50, 100];
  pageIndex = 0;
  active = 'default';
  direction = 'default';

  constructor(
    private dashboardService: DashboardService,
    private datePipe: DatePipe,
    private router: Router,
    private customModalService: CustomModalService,
    private humanizePipe: HumanizePipe
  ) {}

  ngOnInit() {
    this.consultarLeads();
    fromEvent(this.filter.nativeElement, 'keyup')
      .pipe(debounceTime(300), distinctUntilChanged())
      .subscribe(() => {
        if (this.filter.nativeElement.value) {
          const filterValue = this.filter.nativeElement.value
            .trim()
            .toLowerCase();
          this.leadsFiltrados = this.leads.filter((tarea: ITarea) => {
            const { a, b, c, d, e } = this.extraerCriteriosBusqueda(tarea);
            return (a + b + c + d + e).includes(filterValue);
          });
        } else {
          this.leadsFiltrados = this.leads;
        }
      });
  }

  private extraerCriteriosBusqueda(tarea: ITarea) {
    const a = this.datePipe.transform(
      tarea.fechaCreacion,
      'yyyy/MM/dd HH:mm:ss'
    );
    const b = this.humanizePipe.transform(tarea.tipo);
    const c = tarea.nombre.toLowerCase();
    const d = this.datePipe.transform(
      tarea.fechaCreacion,
      'd MMMM / y - h:mm a'
    );
    const e = tarea.criticidad;
    return { a, b, c, d, e };
  }

  changePage($event: PageEvent) {
    this.pageIndex = $event.pageIndex;
    this.pageSize = $event.pageSize;
    this.filter.nativeElement.value = '';
    this.consultarLeads();
  }

  sortData($event) {
    if ($event.direction) {
      this.active = $event.active;
      this.direction = $event.direction;
    } else {
      this.active = 'default';
      this.direction = 'default';
    }
    this.filter.nativeElement.value = '';
    this.consultarLeads();
  }

  verDetalle(lead: ITarea) {
    switch (TipoTarea[lead.tipo]) {
      case TipoTarea.TAREA_DOCUMENTAL:
        this.router.navigate(['/dashboard/detalle-tarea', lead.id]);
        break;
      case TipoTarea.REVISION_LISTAS_NEGRAS:
        this.router.navigate(['/dashboard/revision-listas-negras', lead.id]);
        break;
      default:
        this.router.navigate(['/coaching/detalle-generico', lead.id]);
    }
  }

  private consultarLeads() {
    const offset = this.pageIndex * this.pageSize;
    this.dashboardService
      .consultarTareasPorEstado(
        EstadoTarea.PENDIENTE,
        offset,
        this.pageSize,
        this.active,
        this.direction
      )
      .then((tareasPaginadas: ITareasPaginadas) => {
        this.leads = tareasPaginadas.tareas;
        this.leadsFiltrados = this.leads;
        this.length = tareasPaginadas.cantidad;
      })
      .catch((err) =>
        this.customModalService.abrirModal(CustomModalType.ERROR)
      );
  }
}
