import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Store } from '@ngrx/store';
import { Moment } from 'moment';
import * as momentTimezone from 'moment-timezone';
import { combineLatest, Observable } from 'rxjs';

import { CargandoService } from '@app/shared/servicios/cargando.service';
import { CoachDTO } from '@coaching/modelo/coaches.model';
import {
  DisponibilidadCoaches,
  ModeradorDTO,
} from '@coaching/modelo/disponibilidad-coach.model';
import { Proposito } from '@coaching/modelo/proposito.enum';
import { AgendarSesionAhorradorDTO } from '@coaching/modelo/sesion.model';
import { Tema } from '@coaching/modelo/temas.model';
import { CoachingApiService } from '@coaching/servicios/coaching-api.service';
import { environment } from '@env/environment';

const PROPOSITO_POR_TEMAS = {
  'Solicitud de bloqueado.': Proposito.DISPOSICION_BLOQUEADO,
};

@Component({
  selector: 'app-formulario-agendar-sesion',
  templateUrl: './formulario-agendar-sesion.component.html',
  styleUrls: ['./formulario-agendar-sesion.component.scss'],
})
export class FormularioAgendarSesionComponent implements OnInit {
  @Input() usuario: {
    id: string;
    pais: string;
  };
  @Input() bloquearBoton: boolean;
  @Input() compartirDatos = false;
  @Output() agendarSuccess = new EventEmitter();
  @Output() agendarError = new EventEmitter();
  @Output() horariosError = new EventEmitter();
  minDate: any;
  cargando = false;
  horasConsultadas = false;
  temas: Tema[] = [];
  coaches: CoachDTO[] = [];
  formulario: FormGroup;
  horasDisponibles: DisponibilidadCoaches[] = [];
  temas$: Observable<Tema[]>;
  coaches$: Observable<CoachDTO[]>;
  private actualizandoHoras: boolean;
  private zonaHoraria = environment.zone;

  constructor(
    private fb: FormBuilder,
    private store: Store<any>,
    private cargandoService: CargandoService,
    private apiService: CoachingApiService
  ) {}

  ngOnInit(): void {
    this.crearFormulario();
    this.coaches$ = this.store.select('coaching', 'coaches');
    this.temas$ = this.store.select('coaching', 'temas');
    const zonaHoraria$ = this.store.select('coaching', 'zonaHoraria');
    combineLatest([this.coaches$, this.temas$, zonaHoraria$]).subscribe(
      (value) => {
        this.formulario.reset();
        this.horasDisponibles = [];
        this.coaches = value[0];
        this.temas = value[1];
        this.zonaHoraria = value[2];
        this.minDate = momentTimezone()
          .tz(this.zonaHoraria)
          .format('YYYY-MM-DD');
      }
    );
  }

  get fecha() {
    return this.formulario.get('fecha');
  }

  get hora() {
    return this.formulario.get('hora');
  }

  get coach() {
    return this.formulario.get('coach');
  }

  get tema() {
    return this.formulario.get('tema');
  }

  crearFormulario() {
    this.formulario = this.fb.group({
      fecha: this.fb.control('', [Validators.required]),
      hora: this.fb.control('', [Validators.required]),
      coach: this.fb.control(''),
      tema: this.fb.control('', [Validators.required]),
    });
  }

  disableFormulario() {
    this.fecha.disable();
    this.hora.disable();
    this.coach.disable();
    this.tema.disable();
    this.formulario.disable();
  }

  enableFormulario() {
    this.formulario.enable();
    this.fecha.enable();
    this.hora.enable();
    this.coach.enable();
    this.tema.enable();
  }

  reiniciarFormulario() {
    this.cargando = false;
    this.horasConsultadas = false;
    this.horasDisponibles = [];
    this.formulario.reset();
    this.fecha.markAsPending();
    this.hora.markAsPending();
    this.tema.markAsPending();
    this.coach.markAsPending();
  }

  enviarFormulario() {
    this.cargando = true;
    this.cargandoService.mostrarCargando();
    if (this.formulario.valid) {
      this.agendarCita();
    } else {
      this.cargando = false;
      this.cargandoService.ocultarCargando();
    }
  }

  consultarDisponibilidad() {
    if (this.formulario.get('fecha').valid && !this.actualizandoHoras) {
      this.horasDisponibles = [];
      this.actualizandoHoras = true;
      this.formulario.get('hora').setValue(null);
      const fecha: Moment = this.formulario.getRawValue().fecha;
      const coach: Moment = this.formulario.getRawValue().coach;
      const fechaInicial = this.fechaIncialConZona(fecha);
      this.horasConsultadas = false;
      return this.apiService
        .consultarDisponibilidad(
          environment.envCountry,
          fechaInicial,
          encodeURIComponent(this.zonaHoraria),
          coach ? { moderador: coach } : null
        )
        .then((disponibilidadCoach) => {
          this.horasDisponibles = disponibilidadCoach.disponibilidades;
        })
        .catch((error) => {
          console.error('Error consultando disponibilidad de horas', error);
          this.horasDisponibles = [];
          this.horariosError.emit(true);
        })
        .finally(() => {
          this.horasConsultadas = true;
          this.actualizandoHoras = false;
        });
    }
  }

  agendarCita() {
    const disponibilidadCoaches: DisponibilidadCoaches = this.formulario.getRawValue()
      .hora;
    const tema: Tema = this.temas.find(
      (temaItem) => temaItem.id === this.formulario.getRawValue().tema
    );
    const proposito =
      PROPOSITO_POR_TEMAS[tema?.tema] || Proposito.COACHING_GENERAL;
    const parametros: AgendarSesionAhorradorDTO = {
      fechaFinal: disponibilidadCoaches.periodoTiempo.fin,
      fechaInicial: disponibilidadCoaches.periodoTiempo.inicio,
      idCoach:
        this.formulario.getRawValue().coach ||
        this.obtenerCoachAleatorio(disponibilidadCoaches.moderadoresDisponibles)
          .id ||
        '',
      idAhorrador: this.usuario.id,
      idPaisAhorrador: this.usuario.pais,
      tema: tema ? tema.tema : null,
      notas: '',
      compartirDatosGastos: this.compartirDatos,
      proposito,
    };
    return this.apiService
      .agendar(parametros)
      .then(() => {
        this.actualizandoHoras = false;
        this.agendarSuccess.emit(true);
        this.reiniciarFormulario();
      })
      .catch((error) => {
        console.error('Error agendando sesion', error);
        this.agendarError.emit(true);
      })
      .finally(() => {
        this.cargando = false;
        this.cargandoService.ocultarCargando();
      });
  }

  fechaConZonaSeleccionada(fecha: Date) {
    return momentTimezone(fecha).tz(this.zonaHoraria).format('hh:mm a');
  }

  private fechaIncialConZona(fecha: Moment) {
    return momentTimezone(fecha.format('YYYY-MM-DD HH:mm'))
      .tz(this.zonaHoraria, true)
      .startOf('day')
      .format('YYYY-MM-DDTHH:mm:ss.SSSZ');
  }

  private obtenerCoachAleatorio(moderadoresDisponibles: ModeradorDTO[]) {
    return moderadoresDisponibles[
      Math.floor(Math.random() * moderadoresDisponibles.length)
    ];
  }
}
