import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { Store } from '@ngrx/store';
import { combineLatest, interval, Observable, of, Subscription } from 'rxjs';
import { CoachDTO } from '@app/modulos/coaching/modelo/coaches.model';
import { Tema } from '@app/modulos/coaching/modelo/temas.model';
import { catchError, first, flatMap, map, tap } from 'rxjs/operators';
import { ActivatedRoute, Router } from '@angular/router';
import { Sesion } from '@app/modulos/coaching/modelo/sesion.model';
import { EstadoSesion } from '@app/modulos/coaching/modelo/estado-sesion.enum';
import {
  AhorradorDTO,
  ConsultaTieneEvaluacionAhorradorDTO,
} from '@app/modulos/coaching/modelo/ahorrador.model';
import { CargandoService } from '@app/shared/servicios/cargando.service';

import * as momentTimezone from 'moment-timezone';
import { CustomModalType } from '@app/modulos/dashboard/modelo/enums/CustomModalTypes.enum';
import { CustomModalService } from '@app/shared/servicios/custom-modal.service';
import { DisponibilidadCoaches } from '@app/modulos/coaching/modelo/disponibilidad-coach.model';
import { environment } from '@env/environment';
import { Proposito } from '@app/modulos/coaching/modelo/proposito.enum';
import { SesionesCoach } from '@app/modulos/coaching/modelo/sesionesCoach.model';
import { CONSULTA_SESIONES_COACH_SUCCESS } from '@app/modulos/coaching/state-management/actions/coaching.action';
import { FormularioAgendarSesionComponent } from '@coaching/componentes/formulario-agendar-sesion/formulario-agendar-sesion.component';
import { CoachingApiService } from '@coaching/servicios/coaching-api.service';

@Component({
  selector: 'app-detalle-coaching-usuario',
  templateUrl: './detalle-coaching-usuario.component.html',
  styleUrls: ['./detalle-coaching-usuario.component.scss'],
})
export class DetalleCoachingUsuarioComponent implements OnInit, OnDestroy {
  @ViewChild(FormularioAgendarSesionComponent)
  formulario: FormularioAgendarSesionComponent;

  idUsuario: string;
  idSesion: string;
  cargando = false;
  sesionesTomadas: number;
  columnas: number;
  mostrarDesbloquearAhorro = false;

  horasDisponibles: DisponibilidadCoaches[] = [];
  temas: Tema[] = [];
  coaches: CoachDTO[] = [];
  sesiones: Sesion[] = [];
  sesionesProgramadas: Sesion[] = [];
  sesionesTabla: Sesion[];
  usuario: {
    id: string;
    nombreCompleto: string;
    dineroDisponible: number;
    tieneAhorroDesbloqueado: boolean;
    pais: string;
  };
  ahorradores: AhorradorDTO[] = [];
  sesionesVigentes$: Observable<Sesion[]>;

  temas$: Observable<Tema[]>;
  coaches$: Observable<CoachDTO[]>;
  existeSesionExpirando: boolean;
  sesionSeleccionada: Sesion;
  ahorradores$: Observable<AhorradorDTO[]>;
  isAdmin: boolean;
  tieneEvaluacion = false;
  datosCargados = false;
  bloquearBoton = true;
  diferenciaHoras: number;
  diferenciaMinutos: number;
  private zonaHoraria = environment.zone;
  private suscripciones: Subscription[] = [];

  constructor(
    private fb: FormBuilder,
    private store: Store<any>,
    private router: Router,
    private route: ActivatedRoute,
    private coachingApiService: CoachingApiService,
    private cargandoService: CargandoService,
    private customModalService: CustomModalService
  ) {}

  ngOnDestroy() {
    this.suscripciones.forEach((suscripcion) => suscripcion.unsubscribe());
  }

  onResize(event) {
    this.columnas =
      event.target.innerWidth <= 400
        ? 1
        : event.target.innerWidth <= 600
        ? 2
        : 3;
  }

  ngOnInit() {
    this.existeSesionExpirando = false;
    this.idUsuario = this.route.snapshot.paramMap.get('idUsuario');
    this.idSesion = this.route.snapshot.paramMap.get('idSesion');
    this.columnas =
      window.innerWidth <= 400 ? 1 : window.innerWidth <= 600 ? 2 : 3;
    this.coaches$ = this.store
      .select('coaching', 'coaches')
      .pipe(tap((coaches) => (this.coaches = coaches)));
    this.ahorradores$ = this.store
      .select('ahorradores', 'ahorradores')
      .pipe(tap((ahorradores) => this.ahorradores));
    this.temas$ = this.store
      .select('coaching', 'temas')
      .pipe(tap((temas) => (this.temas = temas)));
    const zonaHoraria$ = this.store.select('coaching', 'zonaHoraria');
    this.sesionesVigentes$ = this.store
      .select('coaching', 'sesionesVigentes')
      .pipe(
        tap((sesiones) => {
          if (
            sesiones.filter((sesion) => sesion.idSesion === this.idSesion)
              .length === 0
          ) {
            this.recargarPagina();
          }
        })
      );
    combineLatest([
      this.coaches$,
      this.temas$,
      zonaHoraria$,
      this.verificarSiUsuarioTieneEvaluacion(),
    ]).subscribe((value) => {
      this.reiniciarFormulario(this.formulario);
      this.horasDisponibles = [];
      this.zonaHoraria = value[2];
      this.cargarDatosSesiones();
      this.cargarDatosDeAhorrador();
      this.tieneEvaluacion = value[3];
      this.datosCargados = true;
    });
    this.store.select('coaching', 'rol').subscribe((value) => {
      this.isAdmin = value === 'ADMIN';
    });
  }

  recargarPagina() {
    this.router.navigateByUrl('');
  }

  private cargarDatosSesiones() {
    this.coachingApiService
      .consultarSesionesAhorrador(this.idUsuario)
      .subscribe((sesionesAhorradorDTO: { sesiones: Sesion[] }) => {
        this.sesiones = this.actualizarFechasSegunZona(
          sesionesAhorradorDTO.sesiones,
          this.zonaHoraria
        );
        this.sesionesTomadas = this.filtrarSesionesTomadas(
          sesionesAhorradorDTO
        );
        this.sesionesProgramadas = this.actualizarFechasSegunZona(
          this.filtrarSesionesAgendadas(sesionesAhorradorDTO),
          this.zonaHoraria
        );
        this.sesionSeleccionada = this.filtrarSesionSeleccionada(
          sesionesAhorradorDTO
        );
        if (this.sesionSeleccionada) {
          this.existeSesionExpirando =
            this.obtenerFecha(
              this.sesionSeleccionada.fechaFin,
              this.zonaHoraria
            ) <= this.obtenerFecha(new Date(), this.zonaHoraria);
          this.programarCalculoDeTiempo();
          this.calcularMostrarDesbloquearAhorro();
        }
        this.cargarDataSource();
        this.cargandoService.ocultarCargando();
      });
  }

  private actualizarFechasSegunZona(
    sesionesVigentes: Sesion[],
    zonaHoraria: string
  ) {
    return sesionesVigentes.map((value) => {
      value.fechaInicioMoment = momentTimezone(value.fechaInicio).tz(
        zonaHoraria
      );
      value.fechaFinMoment = momentTimezone(value.fechaFin).tz(zonaHoraria);
      return value;
    });
  }

  private filtrarSesionSeleccionada(sesionesAhorrador: { sesiones: Sesion[] }) {
    return sesionesAhorrador.sesiones.find(
      (sesion) => sesion.idSesion === this.idSesion
    );
  }

  private filtrarSesionesAgendadas(sesionesAhorrador: { sesiones: Sesion[] }) {
    return sesionesAhorrador.sesiones
      .filter(
        (sesiones) =>
          sesiones.estadoSesion === EstadoSesion.AGENDADA ||
          sesiones.estadoSesion === EstadoSesion.PREAGENDADA ||
          sesiones.estadoSesion === EstadoSesion.AGENDAMIENTO_PROGRAMADO
      )
      .sort((a, b) =>
        a.fechaInicio > b.fechaInicio
          ? 1
          : b.fechaInicio > a.fechaInicio
          ? -1
          : 0
      );
  }

  private filtrarSesionesTomadas(sesionesAhorrador: { sesiones: Sesion[] }) {
    return sesionesAhorrador.sesiones.filter(
      (sesion) =>
        sesion.estadoSesion === EstadoSesion.INICIADA ||
        sesion.estadoSesion === EstadoSesion.FINALIZADA ||
        sesion.estadoSesion === EstadoSesion.FINALIZADA_ERROR
    ).length;
  }

  private cargarDataSource() {
    this.sesionesTabla = this.sesiones.filter(
      (sesion) =>
        sesion.estadoSesion !== EstadoSesion.NUEVA &&
        sesion.estadoSesion !== EstadoSesion.AGENDADA &&
        sesion.estadoSesion !== EstadoSesion.PREAGENDADA &&
        sesion.estadoSesion !== EstadoSesion.AGENDAMIENTO_PROGRAMADO &&
        sesion.estadoSesion !== EstadoSesion.INICIADA
    );
  }

  cargarDatosDeAhorrador() {
    this.coachingApiService.consultarAhorrador(this.idUsuario).subscribe(
      (ahorradorDTO: AhorradorDTO) => {
        this.usuario = {
          id: ahorradorDTO.id,
          nombreCompleto: [
            ahorradorDTO.primerNombre,
            ahorradorDTO.primerApellido,
          ].join(' '),
          dineroDisponible: ahorradorDTO.totalMontoDisponible,
          tieneAhorroDesbloqueado: ahorradorDTO.tieneAhorroDesbloqueado,
          pais: ahorradorDTO.paisAhorro,
        };
      },
      () => {
        this.usuario = null;
      }
    );
  }

  obtenerFecha(fecha: Date, zonaHoraria: string) {
    const hoy = momentTimezone(fecha);
    return hoy.tz(zonaHoraria).toDate();
  }

  desbloquearAhorro() {
    const parametros = {
      idAhorrador: this.idUsuario,
    };

    const modal = this.customModalService.abrirModal(
      CustomModalType.CONFIRMATION,
      { mensaje: '¿Está seguro que desea desbloquear el ahorro?' }
    );

    modal.afterClosed().subscribe((resultado) => {
      if (resultado) {
        this.cargando = true;
        this.cargandoService.mostrarCargando();

        this.coachingApiService.desbloquearAhorro(parametros).then(
          () => {
            this.cargarDatosDeAhorrador();
            this.cargando = false;
            this.cargandoService.ocultarCargando();
            this.mostrarDesbloquearAhorro = false;
            this.mostarModalSuccess('Ahorro desbloqueado exitosamente');
          },
          (error) => {
            console.error('Error desbloqueando ahorro', error);
            this.cargando = false;
            this.mostarModalError('Error desbloqueando ahorro');
          }
        );
      }
    });
  }

  obtenerTabSeleccionado(sesiones: Sesion[]) {
    return sesiones.findIndex((sesion) => sesion.idSesion === this.idSesion);
  }

  cambiarDeTab($event, sesiones: Sesion[]) {
    const indexTabs = $event.index;
    this.sesionSeleccionada = sesiones[indexTabs];
    this.existeSesionExpirando = this.sesionSeleccionada.fechaFin <= new Date();
    if (this.sesionSeleccionada.idSesion !== this.idSesion) {
      this.cargandoService.mostrarCargando();
      this.router
        .navigate([
          `coaching/sesion/${this.sesionSeleccionada.idSesion}`,
          {
            idUsuario: this.sesionSeleccionada.ahorrador.idAhorrador,
          },
        ])
        .then(() => {
          this.idUsuario = this.route.snapshot.paramMap.get('idUsuario');
          this.idSesion = this.sesionSeleccionada.idSesion;
          this.reiniciarFormulario(this.formulario);
          this.cargarDatosSesiones();
          this.cargarDatosDeAhorrador();
        })
        .catch((reason) => console.error(reason));
    }
  }

  reiniciarFormulario(formulario: FormularioAgendarSesionComponent) {
    if (formulario) {
      formulario.crearFormulario();
      formulario.reiniciarFormulario();
    }
  }

  private programarCalculoDeTiempo() {
    this.calcularTiempoFinSesion();
    this.calcularTiempoInicioSesion();
    this.suscripciones.forEach((suscripcion) => suscripcion.unsubscribe());
    this.suscripciones.push(
      interval(5000).subscribe(() => {
        this.calcularTiempoFinSesion();
        this.calcularTiempoInicioSesion();
      })
    );
  }

  private calcularTiempoInicioSesion() {
    const fechaInicioSesion = new Date(this.sesionSeleccionada.fechaInicio);
    fechaInicioSesion.setMinutes(fechaInicioSesion.getMinutes() + 10);
    const fechaInicio = this.obtenerFecha(fechaInicioSesion, this.zonaHoraria);
    const fechaActual = this.obtenerFecha(new Date(), this.zonaHoraria);
    this.bloquearBoton = fechaInicio.getTime() > fechaActual.getTime();
  }

  private calcularTiempoFinSesion() {
    const fechaFinSesion = this.sesionSeleccionada.fechaFinMoment.toDate();
    fechaFinSesion.setMinutes(fechaFinSesion.getMinutes() + 10);
    const fechaFin = this.obtenerFecha(fechaFinSesion, this.zonaHoraria);
    const fechaActual = this.obtenerFecha(new Date(), this.zonaHoraria);
    const diffMs = fechaFin.getTime() - fechaActual.getTime();
    this.diferenciaHoras = Math.floor((diffMs % 86400000) / 3600000);
    this.diferenciaMinutos = Math.round(
      ((diffMs % 86400000) % 3600000) / 60000
    );
  }

  private calcularMostrarDesbloquearAhorro() {
    this.mostrarDesbloquearAhorro =
      this.sesionSeleccionada.proposito === Proposito.DISPOSICION_BLOQUEADO;
  }

  mostarModalSuccess(mensaje) {
    this.customModalService.abrirModal(CustomModalType.SUCCESS, { mensaje });
  }

  mostarModalError(mensaje) {
    this.customModalService.abrirModal(CustomModalType.ERROR, { mensaje });
  }

  public recargarConsulta() {
    this.store
      .select('usuario', 'uuid')
      .pipe(first())
      .pipe(
        flatMap((idCoach) => {
          return this.coachingApiService.consultarSesiones(idCoach);
        })
      )
      .subscribe((sesionesCoach: SesionesCoach) => {
        this.ordenarSesionesVigentes(sesionesCoach);
      });
  }

  public ordenarSesionesVigentes(sesionesCoach: SesionesCoach) {
    const sesionesVigentes: SesionesCoach = { ...sesionesCoach };
    sesionesVigentes.sesionesVigentes = sesionesVigentes.sesionesVigentes
      .filter((sesion) => sesion.estadoSesion !== EstadoSesion.VENCIDA)
      .sort((a, b) => new Date(b.fechaInicio).getTime() - new Date(a.fechaInicio).getTime());
    this.store.dispatch({
      type: CONSULTA_SESIONES_COACH_SUCCESS,
      payload: sesionesVigentes,
    });
  }

  verificarSiUsuarioTieneEvaluacion(): Observable<boolean> {
    return this.coachingApiService
      .consultarUsuarioTieneEvaluacion(this.idUsuario)
      .pipe(
        map((consultaTieneEvaluacion: ConsultaTieneEvaluacionAhorradorDTO) => {
          return consultaTieneEvaluacion.tieneEvaluacion;
        }),
        catchError((error) => {
          console.error(
            'Error verificando si el usuario tiene evaluacion',
            error
          );
          this.mostarModalError(
            'Error verificando si el usuario tiene evaluacion'
          );
          return of(false);
        })
      );
  }
}
