import { ActualizarSesion } from './../modelo/sesion.model';
import { Injectable } from '@angular/core';
import { ApiService } from '@core/servicios/api.service';
import { DisponibilidadesCoaches } from '@coaching/modelo/disponibilidad-coach.model';
import { AgendarSesionAhorradorDTO } from '@coaching/modelo/sesion.model';
import { EncryptService } from '@qiip/core';
import { fromPromise } from 'rxjs/internal-compatibility';

@Injectable({
  providedIn: 'root',
})
export class CoachingApiService {
  constructor(
    private api: ApiService,
    private encryptService: EncryptService
  ) {}

  consultarDisponibilidad(
    pais: string,
    fecha: string,
    zonaHoraria: string,
    queryParams?: any
  ): Promise<DisponibilidadesCoaches> {
    return this.api.get(
      `coaching/v2/${pais}/coaches/disponibilidad/${fecha}/${zonaHoraria}`,
      queryParams,
      {}
    );
  }

  agendar(body: AgendarSesionAhorradorDTO): Promise<any> {
    return this.cifrarId(body.idAhorrador).then((nbmAuth) =>
      this.api.post(`coaching/v2/ahorradores/agendar_cita`, body, {
        'X-nbm-auth': nbmAuth,
      })
    );
  }

  actualizarSesion(data): Promise<any> {
    return this.api.post(`coaching/v2/coaches/actualizar_sesion`, data);
  }

  consultarSesiones(idCoach) {
    return fromPromise(this.api.get(`coaching/coaches/${idCoach}/sesiones`));
  }

  cifrarId(idUsuario: string) {
    return this.api
      .get('cash_in/llavePublica')
      .then((llave) =>
        this.encryptService.cifrar(idUsuario, llave.llavePublica)
      );
  }

  consultarNombresUsuario(queryParams) {
    return fromPromise(
      this.api.get(`usuario/usuarios/datos/nombres`, queryParams)
    );
  }

  consultarAhorrador(idUsuario) {
    return fromPromise(this.api.get(`ahorro/ahorradores/${idUsuario}`));
  }

  consultarSesionesAhorrador(idAhorrador) {
    return fromPromise(
      this.api.get(`coaching/ahorradores/${idAhorrador}/sesiones`)
    );
  }

  actualizarEstadoSesion(sesion: ActualizarSesion): Promise<any>{
    return this.api.post('coaching/v2/coaches/actualizar_estado_sesion', sesion);
  }

  consultaMotivos(): Promise<any> {
    return this.api.get('coaching/v2/sesiones/motivos');
  }

  consultarSesionesAgendadasCoach(idCoach, zonaHoraria) {
    return fromPromise(
      this.api.get(
        `coaching/v2/coaches/${idCoach}/sesiones/agendadas/${zonaHoraria}`
      )
    );
  }

  desbloquearAhorro(data: { idAhorrador: string }) {
    return this.api.post(`ahorro/desbloquear_ahorro`, data);
  }

  consultarUsuarioTieneEvaluacion(idUsuario: string) {
    return fromPromise(
      this.api.get(`servicio/evaluaciones/tiene_evaluacion/${idUsuario}`)
    );
  }

  confirmarOTP(data: { otp: any; idAhorrador: any; flujoOtp: string }) {
    return this.api.post(`legal/confirmar_otp`, data);
  }

  compartirResultadosEvaluacion(data: {
    idSesion: string;
    compartirResultados: boolean;
  }) {
    return this.api.post(`coaching/compartir_resultados_bienestar`, data);
  }

  consultarEvaluacion(queryParams) {
    return fromPromise(this.api.get(`servicio/evaluaciones`, queryParams));
  }

  consultarTemas(queryParams: { tipoUsuario: string; pais: string }) {
    return this.api.get(`coaching/sesiones/temas`, queryParams);
  }

  consultarCoachesPorPais(pais: string) {
    return fromPromise(this.api.get(`coaching/v2/${pais}/coaches`));
  }
}
