import { CustomModalType } from './../../../dashboard/modelo/enums/CustomModalTypes.enum';
import { CustomModalService } from './../../../../shared/servicios/custom-modal.service';
import { ActualizarSesion } from './../../modelo/sesion.model';
import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { combineLatest, interval, Observable, Subscription } from 'rxjs';
import { Sesion } from '@app/modulos/coaching/modelo/sesion.model';
import { Router } from '@angular/router';
import { first, flatMap, map } from 'rxjs/operators';
import { AhorradorDTO } from '@app/modulos/coaching/modelo/ahorrador.model';
import * as momentTimezone from 'moment-timezone';
import { EstadoSesion } from '@app/modulos/coaching/modelo/estado-sesion.enum';
import { environment } from '@env/environment';
import { SesionesCoach } from '@app/modulos/coaching/modelo/sesionesCoach.model';
import { CONSULTA_SESIONES_COACH_SUCCESS } from '@app/modulos/coaching/state-management/actions/coaching.action';
import { CoachingApiService } from '@coaching/servicios/coaching-api.service';
import { CONSULTA_SESIONES_COACH_EXPIRED } from '../../state-management/actions/coaching.action';
import { Motivo } from '../../modelo/motivo';

@Component({
  selector: 'app-inicio',
  templateUrl: './inicio.component.html',
  styleUrls: ['./inicio.component.scss'],
})
export class InicioComponent implements OnInit {

  readonly MENSAJE_CAMBIO_ESTADO_SESION ='¡Se cambio el estado de la sesión correctamente!';
  readonly MENSAJE_CAMBIO_ESTADO_SESION_ERROR='¡Ocurrió un error cambiando el estado de la sesión!';
  ahorradores: any = {};
  EstadoSesion = EstadoSesion;
  sesionesVigentes$: Observable<Sesion[]>;
  sesionesVencidas: Sesion[];
  motivos: Motivo[];
  fechaActual: Date;
  tiemposParaTerminarSesiones: {
    minutos: number;
    segundos: number | string;
  }[] = [];
  zonaHoraria = environment.zone;
  private suscripciones: Subscription[];

  constructor(
    private store: Store<any>,
    private router: Router,
    private customModalService: CustomModalService,
    private coachingApiService: CoachingApiService
  ) {
    this.suscripciones = [];
  }

  ngOnInit() {
    this.consultarMotivos();
    const zonaHoraria$ = this.store.select('coaching', 'zonaHoraria');
    this.fechaActual = this.obtenerFecha(new Date(), this.zonaHoraria);
    const sesionesVencidasCoach$ = this.store
      .select('coaching', 'sesionesVencidas')
      .pipe(map((sesiones: Sesion[]) => this.ordenar(sesiones)));

    const sesiones$ = this.store
      .select('coaching', 'sesionesVigentes')
      .pipe(map((sesiones: Sesion[]) => this.ordenar(sesiones)));

    this.sesionesVigentes$ = combineLatest([
      zonaHoraria$,
      sesiones$,
      sesionesVencidasCoach$,
    ]).pipe(
      map((value) => {
        if (value[2]) {
          this.sesionesVencidas = this.filtrarFecha(value[2]);
        }
        if (value[1]) {
          this.zonaHoraria = value[0];
          this.cargarDatosDeAhorradoresDeSesiones(value[1]);
          this.calcularTiempos(value[1], this.zonaHoraria);
          this.suscripciones.forEach((suscripcion) =>
            suscripcion.unsubscribe()
          );
          this.suscripciones.push(
            interval(1000).subscribe(() => {
              this.calcularTiempos(value[1], this.zonaHoraria);
            })
          );
          return value[1];
        }
      })
    );
  }

  filtrarFecha(sesiones:Sesion[]){
    return sesiones.filter((fechaSesion) =>
          fechaSesion.fechaInicioMoment.format('DD/MM/YYYY')
          === momentTimezone(this.fechaActual).format('DD/MM/YYYY'));
  }

  consultarMotivos() {
    this.coachingApiService.consultaMotivos().then((value) => {
      this.motivos = value.motivos;
    });
  }

  actualizarEstado(sesionActualizar: any) {
    const body: ActualizarSesion = {
      idSesion: sesionActualizar.idSesion,
      estado: EstadoSesion.FINALIZADA_POR_TELEFONO,
      motivo: sesionActualizar.motivo,
    };
    this.coachingApiService.actualizarEstadoSesion(body).then(() => {
      this.recargarConsulta();
      this.mostarModalSuccess(this.MENSAJE_CAMBIO_ESTADO_SESION);
    },(err) => this.mostarModalError(this.MENSAJE_CAMBIO_ESTADO_SESION_ERROR));
  }

  private ordenar(sesiones: Sesion[]) {
    return sesiones
      .slice()
      .sort((a, b) => new Date(a.fechaInicio).getTime() - new Date(b.fechaInicio).getTime());
  }

  mostarModalSuccess(mensaje) {
    this.customModalService.abrirModal(CustomModalType.SUCCESS, { mensaje });
  }

  mostarModalError(mensaje) {
    this.customModalService.abrirModal(CustomModalType.ERROR, { mensaje });
  }

  abrirDetalle(sesion: Sesion) {
    this.router.navigate([
      `coaching/sesion/${sesion.idSesion}`,
      {
        idUsuario: sesion.ahorrador.idAhorrador,
      },
    ]);
  }

  cargarDatosDeAhorradoresDeSesiones(sesionesVigentes) {
    const ahorradores = sesionesVigentes
      .map((sesion) => sesion.ahorrador.idAhorrador)
      .filter(this.filtrarUnicos)
      .map((id) => this.coachingApiService.consultarAhorrador(id));
    combineLatest(ahorradores).subscribe((ahorradoresResponse) => {
      const lista = ahorradoresResponse as AhorradorDTO[];
      lista.forEach(
        (ahorrador) => (this.ahorradores[ahorrador.id] = ahorrador)
      );
    });
  }

  filtrarUnicos(value, index, self) {
    return self.indexOf(value) === index;
  }

  obtenerClaseSesion(sesion: Sesion) {
    let clase = '';
    const hoy = this.obtenerFecha(new Date(), this.zonaHoraria);
    const fechaInicio = sesion.fechaInicioMoment.toDate();
    const fechaFin = sesion.fechaFinMoment.toDate();
    if (fechaFin < hoy) {
      clase = 'sesion-expirando';
    } else if (fechaFin >= hoy && fechaInicio <= hoy) {
      clase = 'sesion-activa';
    } else {
      clase = 'sesion-agendada';
    }
    return clase;
  }

  obtenerFecha(fecha: Date, zonaHoraria: string) {
    const moment = momentTimezone(fecha);
    return moment.tz(zonaHoraria).toDate();
  }

  private calcularTiempos(sesiones: Sesion[], zonaHoraria: string) {
    this.tiemposParaTerminarSesiones = sesiones.map((sesion) => {
      return this.calcularTiempoFinSesion(sesion, zonaHoraria);
    });
  }

  private calcularTiempoFinSesion(sesion: Sesion, zonaHoraria: string) {
    const fechaFinSesion = new Date(sesion.fechaFin);
    fechaFinSesion.setMinutes(fechaFinSesion.getMinutes() + 10);
    const f1 = this.obtenerFecha(fechaFinSesion, zonaHoraria);
    const f2 = this.obtenerFecha(new Date(), zonaHoraria);
    const tiempo = f1.getTime() - f2.getTime();
    const minutos = Math.floor(tiempo / 60000);
    const segundos = (tiempo % 60000) / 1000;
    return {
      minutos: minutos >= 0 ? minutos : 0,
      segundos: segundos >= 0 ? segundos.toFixed(0) : 0,
    };
  }

  public recargarConsulta() {
    this.store
      .select('usuario', 'uuid')
      .pipe(first())
      .pipe(
        flatMap((idCoach) => {
          return this.coachingApiService.consultarSesiones(idCoach);
        })
      )
      .subscribe((sesionesCoach: SesionesCoach) => {
        this.ordenarSesionesVigentes(sesionesCoach);
        this.ordenarSesionesVencidas(sesionesCoach);
      });
  }

  public ordenarSesionesVigentes(sesionesCoach: SesionesCoach) {
    const sesionesVigentes: SesionesCoach = { ...sesionesCoach };
    sesionesVigentes.sesionesVigentes = this.ordenarSesion(
      sesionesVigentes.sesionesVigentes.filter(
        (sesion) => sesion.estadoSesion !== EstadoSesion.VENCIDA
      )
    );

    this.store.dispatch({
      type: CONSULTA_SESIONES_COACH_SUCCESS,
      payload: sesionesVigentes,
    });
  }

  public ordenarSesionesVencidas(sesionesCoach: SesionesCoach) {
    const sesionesVencidas: SesionesCoach = { ...sesionesCoach };
    sesionesVencidas.sesionesVigentes = this.ordenarSesion(
      sesionesVencidas.sesionesVigentes.filter(
        (sesion) => sesion.estadoSesion === EstadoSesion.VENCIDA
      )
    );

    this.store.dispatch({
      type: CONSULTA_SESIONES_COACH_EXPIRED,
      payload: sesionesVencidas,
    });
  }

  public ordenarSesion(sesiones: Sesion[]) {
    return sesiones.sort((a, b) => new Date(b.fechaInicio).getTime() - new Date(a.fechaInicio).getTime());
  }
}
