import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import {
  DisponibilidadCoaches,
  DisponibilidadesCoaches,
} from '@app/modulos/coaching/modelo/disponibilidad-coach.model';
import { Moment } from 'moment';
import * as momentTimezone from 'moment-timezone';
import { environment } from '@env/environment';
import { CustomModalService } from '@app/shared/servicios/custom-modal.service';
import { CustomModalType } from '@app/modulos/dashboard/modelo/enums/CustomModalTypes.enum';
import { Observable, Subscription } from 'rxjs';
import { CargandoService } from '@app/shared/servicios/cargando.service';
import { Store } from '@ngrx/store';
import { Sesion } from '@app/modulos/coaching/modelo/sesion.model';
import { SesionAgendadas } from '@app/modulos/coaching/modelo/sesionesAgendadas.model';
import { CoachingApiService } from '@coaching/servicios/coaching-api.service';

@Component({
  selector: 'app-lista-coaching-usuario-mensual-edicion',
  templateUrl: './lista-coaching-usuario-mensual-edicion.component.html',
  styleUrls: ['./lista-coaching-usuario-mensual-edicion.component.scss'],
})
export class ListaCoachingUsuarioMensualEdicionComponent implements OnInit {
  formulario: FormGroup;
  horas: DisponibilidadCoaches[] = [];
  idUsuario: string;
  horasConsultadas = false;
  coaches: Array<{
    id: string;
    urlImagen: string;
    nombre: string;
    idSesion: string;
  }> = [];
  cargando = false;
  sesiones: Sesion[] = [];
  sesionesVigentes$: Observable<Sesion[]>;
  zonaHoraria = environment.zone;
  private actualizandoHoras: boolean;
  private suscripciones: Subscription[] = [];
  private isAdmin: boolean;
  private idSesion: string;
  minDate: any;
  disableActualizar = false;

  constructor(
    private fb: FormBuilder,
    private store: Store<any>,
    private coachingApiService: CoachingApiService,
    private customModalService: CustomModalService,
    private cargandoService: CargandoService,
    public dialogRef: MatDialogRef<ListaCoachingUsuarioMensualEdicionComponent>,
    @Inject(MAT_DIALOG_DATA) public data: SesionAgendadas
  ) {}

  ngOnInit() {
    this.suscripciones.push(
      this.store
        .select('coaching', 'idSesion')
        .subscribe((idSesion) => (this.idSesion = idSesion))
    );
    this.formulario = this.crearFormulario(this.fb);
    this.suscripciones.push(
      this.store.select('coaching', 'rol').subscribe((value) => {
        this.isUserAdmin = value === 'ADMIN';
      })
    );
    this.suscripciones.push(
      this.store.select('coaching', 'zonaHoraria').subscribe((value) => {
        this.zonaHoraria = value;
        this.minDate = momentTimezone()
          .tz(this.zonaHoraria)
          .format('YYYY-MM-DD');
      })
    );
  }

  crearFormulario(fb: FormBuilder): FormGroup {
    return fb.group({
      fecha: fb.control('', [Validators.required]),
      hora: fb.control('', [Validators.required]),
      coach: fb.control({ value: '', disabled: true }),
    });
  }

  horasOnChange(event) {
    this.formulario.get('coach').setValue('');
    if (event.value && event.value.moderadoresDisponibles) {
      this.formulario.get('coach').enable();
      this.coaches = event.value.moderadoresDisponibles;
      if (this.coaches.length === 1) {
        this.formulario.get('coach').setValue(this.coaches[0].id);
      }
    }
  }

  consultarDisponibilidad() {
    if (this.formulario.get('fecha').valid && !this.actualizandoHoras) {
      this.horas = [];
      this.coaches = [];
      this.actualizandoHoras = true;
      const fecha: Moment = this.formulario.get('fecha').value;
      this.formulario.get('hora').setValue(null);
      this.formulario.get('coach').setValue(null);
      this.formulario.get('coach').disable();
      this.cargando = true;
      this.cargandoService.mostrarCargando();
      this.coachingApiService
        .consultarDisponibilidad(
          environment.envCountry,
          this.fechaIncialConZona(fecha),
          encodeURIComponent(this.zonaHoraria),
          null
        )
        .then(
          (disponibilidadesCoaches: DisponibilidadesCoaches) => {
            this.horas = disponibilidadesCoaches.disponibilidades;
            this.cargando = false;
            this.cargandoService.ocultarCargando();
            this.actualizandoHoras = false;
            this.horasConsultadas = true;
          },
          (error) => {
            console.error('Error consultando disponibilidad de horas', error);
            this.horas = [];
            this.actualizandoHoras = false;
            this.mostarModalErrorHorarios();
            this.horasConsultadas = true;
            this.cargando = false;
            this.cargandoService.ocultarCargando();
          }
        );
    }
  }

  private fechaIncialConZona(fecha: Moment) {
    return momentTimezone(fecha.format('YYYY-MM-DD HH:mm'))
      .tz(this.zonaHoraria, true)
      .startOf('day')
      .format('YYYY-MM-DDTHH:mm:ss.SSSZ');
  }

  horaConZona(fecha: Date) {
    return momentTimezone(fecha).tz(this.zonaHoraria).format('hh:mm a');
  }

  mostarModalErrorHorarios() {
    this.customModalService.abrirModal(CustomModalType.ERROR, {
      mensaje: 'Error consultando horarios de disponibilidad.',
    });
  }

  limpiarFormulario() {
    this.formulario.get('fecha').setValue(null);
    this.formulario.get('hora').setValue(null);
    this.formulario.get('coach').setValue(null);
  }

  closeModalAfterUpdate() {
    this.limpiarFormulario();
    this.dialogRef.close();
  }

  mostarModalSuccess(mensaje) {
    this.customModalService.abrirModal(
      CustomModalType.SUCCESS,
      { mensaje },
      () => {
        this.closeModalAfterUpdate();
      }
    );
  }

  mostarModalError(mensaje) {
    this.customModalService.abrirModal(
      CustomModalType.ERROR,
      { mensaje },
      () => {
        this.closeModalAfterUpdate();
      }
    );
  }

  actualizarSesion() {
    this.cargando = true;
    this.disableActualizar = true;
    this.cargandoService.mostrarCargando();
    if (this.formulario.valid) {
      const hora: DisponibilidadCoaches = this.formulario.get('hora').value;
      const parametros = {
        fechaInicio: hora.periodoTiempo.inicio,
        fechaFin: hora.periodoTiempo.fin,
        zonaHoraria: this.zonaHoraria,
        idCoachAnterior: this.data.idCoach,
        idCoachActual: this.formulario.get('coach').value,
        idAhorrador: this.data.idAhorrador,
        idCitaAnterior: this.data.idSesion,
      };
      this.coachingApiService.actualizarSesion(parametros).then(
        () => {
          this.limpiarFormulario();
          this.actualizandoHoras = false;
          this.cargando = false;
          this.cargandoService.ocultarCargando();
          this.mostarModalSuccess('¡La sesión se actualizó con éxito!');
        },
        (error) => {
          this.limpiarFormulario();
          console.error('Error agendando sesion', error);
          this.cargando = false;
          this.cargandoService.ocultarCargando();
          this.mostarModalError(
            'Error al actualizar la sesión, revisa los horarios disponibles e intenta de nuevo.'
          );
        }
      );
    } else {
      this.cargando = false;
      this.cargandoService.ocultarCargando();
    }
  }

  get coach() {
    return this.formulario.get('coach');
  }
  get fecha() {
    return this.formulario.get('fecha');
  }

  get hora() {
    return this.formulario.get('hora');
  }

  get isUserAdmin(): boolean {
    return this.isAdmin;
  }
  set isUserAdmin(val: boolean) {
    this.isAdmin = val;
  }

  obtenerFecha(fecha: Date, zonaHoraria: string) {
    const hoy = momentTimezone(fecha);
    return hoy.tz(zonaHoraria).toDate();
  }

  onNoClick(): void {
    this.dialogRef.close();
  }
}
