import { Component, Input, OnInit } from '@angular/core';
import { MatCheckboxChange } from '@angular/material/checkbox';
import { CustomModalService } from '@app/shared/servicios/custom-modal.service';
import { CustomModalType } from '@app/modulos/dashboard/modelo/enums/CustomModalTypes.enum';
import { ConsultaEvaluacionAhorradorDTO } from '@app/modulos/coaching/modelo/ahorrador.model';
import { Sesion } from '@app/modulos/coaching/modelo/sesion.model';
import { CoachingApiService } from '@coaching/servicios/coaching-api.service';

@Component({
  selector: 'app-detalle-bienestar-financiero',
  templateUrl: './detalle-bienestar-financiero.component.html',
  styleUrls: ['./detalle-bienestar-financiero.component.scss'],
})
export class DetalleBienestarFinancieroComponent implements OnInit {
  detalleToogle = false;
  checkHabilitado = true;
  secciones = [];
  datosEvaluacion: ConsultaEvaluacionAhorradorDTO;

  @Input() idUsuario: string;
  @Input() idSesion: string;
  @Input() compartirResultadoEvaluacionBienestar: boolean;

  constructor(
    private coachingApiService: CoachingApiService,
    private modalService: CustomModalService
  ) {}

  ngOnInit() {
    if (this.compartirResultadoEvaluacionBienestar) {
      this.checkHabilitado = false;
      this.compartirResultadoBienestarFinanciero();
    }
  }

  toogle() {
    this.detalleToogle = !this.detalleToogle;
  }

  checkboxSeleccionado($event: MatCheckboxChange) {
    if ($event.checked) {
      this.checkHabilitado = false;
      this.compartirResultadoBienestarFinanciero();
    }
  }

  compartirResultadoBienestarFinanciero() {
    const parametros = { compartirResultados: true, idSesion: this.idSesion };
    this.coachingApiService.compartirResultadosEvaluacion(parametros).then(
      (sesion: Sesion) => {
        this.servicioEvaluaciones();
        console.log(
          'Se ha compartido los resultados de bienestar financiero: ' +
            sesion.compartirEvaluacionBienestar
        );
      },
      (error) => {
        console.error(
          'Error al compartir los resultados de bienestar financiero',
          error
        );
      }
    );
  }

  servicioEvaluaciones() {
    const paramsEvaluacion = {
      idEvaluacion: null,
      idComprador: this.idUsuario,
    };
    this.coachingApiService.consultarEvaluacion(paramsEvaluacion).subscribe(
      (value: ConsultaEvaluacionAhorradorDTO) => {
        this.secciones = Object.keys(value.puntajes);
        this.datosEvaluacion = value;
      },
      (error) => {
        console.error(error);
        this.modalService.abrirModal(CustomModalType.ERROR, {
          mensaje: 'Error consultando score de bienestar financiero.',
        });
      }
    );
  }
}
