import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { ViewChild, Input, Component, Output, EventEmitter } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { Sesion } from '../../modelo/sesion.model';
import { Motivo } from '../../modelo/motivo';
import { TablaModelVencida } from '../../modelo/tabla-sesiones-vencidas';
@Component({
  selector: 'app-lista-sesiones-vencidas',
  templateUrl: './lista-sesiones-vencidas.component.html',
  styleUrls: ['./lista-sesiones-vencidas.component.scss'],
})

export class ListaSesionesVencidasComponent {

  _sesiones: Sesion[] = [];

  @Input()
  motivos: Motivo[] = [];
  @Output()
  actualizarSesion = new EventEmitter();

  displayedColumns: string[] = ['fecha', 'hora', 'estado', 'motivo', 'accion'];
  dataSource: MatTableDataSource<TablaModelVencida>;
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  @Input()
  set sesiones(sesiones: Sesion[]) {
    this._sesiones = this.ordenar(sesiones);
    const datosTabla = this._sesiones.map((sesion) => {
      return {
        idSesion: sesion.idSesion,
        fecha: sesion.fechaInicioMoment.format('DD/MM/YYYY'),
        hora: sesion.fechaInicioMoment.format('h:mm a') +
          ' - ' + sesion.fechaFinMoment.format('h:mm a'),
        estado: sesion.estadoSesion.toString(),
        motivo: ''
      };
    });
    this.refrescarDatosTabla(datosTabla);
  }

  private ordenar(sesiones: Sesion[]) {
    return sesiones.sort((a, b) => new Date(b.fechaInicio).getTime() - new Date(a.fechaInicio).getTime());
  }

  cambiarMotivo(sesion: any, motivo: string) {
    sesion.motivo = motivo;
  }

  cambiarEstado(sesion: any) {
    this.actualizarSesion.emit(sesion);
  }

  refrescarDatosTabla(datosTabla) {
    this.dataSource = new MatTableDataSource(datosTabla);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }
}
